body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    min-height: 100vh;
}
.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  min-height: 100vh;
}

.app-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.app-article {
    margin-top: 50px;
    margin-left: 5%;
    width: 90%;
}
.navbar {
    background-color: #181a1f;
    height: 50px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

.navbar-content {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 100%;
    margin: 0 auto;
    width: 90%;
}

.nav-link {
    color: #ffffff;
    font-size: 1em;
    padding: 0 10px;
    text-decoration: none;
}

.navbar-links {
    height: 100%;
}

.navbar-icon {
    fill: white;
    height: 100%;
}

@media (max-width: 599px) and (min-width: 0px) {
    .nav-link {
        font-size: calc(0.5em + 6px);
    }
}

@media (max-width: 370px) and (min-width: 0px) {
    .nav-link {
        font-size: calc(0.5em + 3px);
    }
}
h1 {
    font-size: 36px;
    -webkit-margin-before: 0m;
            margin-block-start: 0m;
    -webkit-margin-after: 0;
            margin-block-end: 0;
}

p {
    font-size: 18px;
}

code {
    background: #efefef;
    font-size: 16px;
    padding: 2px 5px;
}

.codeblock {
    background-color: #181a1f;
    border-radius: 10px;
    color: #fff;
    font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    max-width: 800px;
    padding: 10px 10px;
    overflow-x: auto;
}

.code-line {
    display: block;
}

.keyword {
    color: #c586c0;
}

.string {
    color: #ce9178;
}

.punctuation {
    color: #fff;
}

.func {
    color: #9cdcfe;
}

.prop {
    color: #9cdcfe;
}

.element {
    color: #569cd6;
}

@media (max-width: 599px) and (min-width: 0px) {
    p {
        font-size: 16px;
    }
    code {
        font-size: 14px;
    }
    .codeblock {
        font-size: 14px;
    }
}
